* {
  box-sizing: border-box;
}

.page {
  font-family: Roboto, Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.page__body {
  margin: 0;
}

.container {
  justify-content: flex-end;
  align-items: center;
  margin: 0;
  display: flex;
}

.img {
  width: 40px;
  height: 40px;
  margin: 10px auto 10px 50px;
}

.nav {
  text-transform: uppercase;
  margin-right: 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
}

.nav__link {
  color: #060b35;
  margin-right: 20px;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  transition-duration: .3s;
  display: block;
}

.video__link {
  margin-right: 0;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 4px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.main {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-count: 4;
  }
}

.product {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.product:hover {
  transform: scale(1.2);
}

.product:hover .product__title {
  color: #34568b;
}

.product__photo {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  transition: transform .3s;
  display: block;
}

.product__photo:hover {
  transform: scale(1.05);
}

.product__title {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition-duration: .3s;
}

.product__text-code-paragraph {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 0;
}

.product__stars {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  margin-bottom: 0;
  display: flex;
}

.product__text-stars-paragraph {
  color: #060b35;
  margin: 0;
}

.product__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  margin-bottom: 0;
  display: flex;
}

.product__text-price-paragraph {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.product__number-price-paragraph {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product__button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  margin-bottom: 0;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition-duration: .3s;
}

.product__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  margin: 0;
  padding: 0;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
